import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ValeurStockComponent } from './valeur-stock.component';

const routes: Routes = [
  {
    path: '',
    component: ValeurStockComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ValeurStockRoutingModule { }
